.bg_img {
  background-image: url("/img/landing/img/Mask\ Group\ 12.png");
}

.font_style {
  color: white;
  font-size: 95px;
  text-align: center;
  font-weight: 900;
}

.btn_style {
  font-size: 21px;
  color: white;
  background-color: #ff6cc4;
  width: 50%;
  line-height: 2.5;
  box-shadow: 0px 8px 15px #ffc0e6;
}

/* .form_style {
    margin: 30px 20px 18px 20px;
} */

.nav-link {
  color: red;
  font-size: 20px;
  padding: 10px;
  color: black !important;
  font-weight: 600;
}

.nav-link span:hover {
  border-bottom: 2px solid #de0c89;
}

.form-control {
  background-color: #e9ecef;
  line-height: 2.5;
  padding-left: 30px;
}

.custom-select {
  background-color: #e9ecef;
  height: 3rem;
  padding-left: 30px;
}

/* .card {
    margin-right: 2.5rem;
} */

::placeholder {
  color: silver;
}

.head {
  padding-left: 120px;
}

@media (max-width: 480px) {
  .font_style {
    color: white;
    font-size: 50px;
    text-align: center;
    font-weight: 900;
  }
  .card {
    margin-right: 0px !important;
  }
  .head {
    width: 20rem;
    height: 5rem;
    margin-top: -83px;
    padding-left: 0px !important;
  }
  .nav-link {
    padding-left: 0 !important;
  }
  .hhh {
    padding-left: 48px !important;
  }
}
