.container {
  @media screen and (min-width: 768px) {
    width: 100%;
    max-width: 1630px;
  }
}

img.logo__img {
  width: 22%;
}

.header-section .nav-link.active,
.header-section .nav-link:hover {
  color: #b519a9 !important;
}

button.btn.custome.button.style-banner,
button.btn.custome.button.style-about {
  cursor: pointer;
  background: #b519a9;
  color: #ffffff;
  border-radius: 55px;
  padding: 12px 36px;
  font-size: 14px;
  font-family: "Nunito Sans SemiBold";
}

.btn:before {
  position: absolute;
  height: 0;
  width: 0;
  border-radius: 50%;
  background-color: #b5197c;
  transition: width 0.3s ease-in-out, height 0.3s ease-in-out;
  z-index: -1;
  content: "";
  top: 0;
}
header#header-section {
  background: #b519c0;
}

.navbar-light .navbar-nav .nav-link {
  color: white;
}

.header-section .nav-link.active,
.header-section .nav-link:hover {
  color: white !important;
}

.styling-section {
  padding-top: 150px;
  padding-bottom: 50px;
}

.header-section .nav-link.active::before,
.header-section .nav-link:hover::before {
  content: "";
  display: block;
  position: absolute;
  width: 15px;
  height: 5px;
  background: white;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  border-radius: 20px;
}

.container__wrap {
  @include directify($directions) {
    #{directed('padding-left')}: 0px;
  }
  padding-top: 90px;
  min-height: 100vh;
  transition: padding-left 0.3s;

  @include themify($themes) {
    background: themed("colorBackgroundBody");
  }

  @media screen and (min-width: 576px) {
    @include directify($directions) {
      #{directed('padding-left')}: 250px;
    }
  }

  @media screen and (max-width: 576px) {
    padding-top: 150px;
  }
}

.layout {
  &.layout--collapse {
    & + .container__wrap {
      @include directify($directions) {
        #{directed('padding-left')}: 0px;
      }

      @media screen and (min-width: 576px) {
        @include directify($directions) {
          #{directed('padding-left')}: 60px;
        }
      }
    }
  }

  &.layout--top-navigation {
    & + .container__wrap {
      @media screen and (min-width: 576px) {
        @include directify($directions) {
          #{directed('padding-left')}: 0px;
        }
      }
    }
  }
}
